<template>
  <div class="activity-management">
    <template v-if="$route.matched.length <= 2">
      <el-new-card>
        <template slot="header">{{ $route.meta.name }}</template>

        <el-form inline size="small" :model="searchForm" ref="searchForm">
          <el-form-item prop="activity">
            <el-input v-model="searchForm.activity" placeholder="输入活动名称"></el-input>
          </el-form-item>
          <el-form-item prop="deptId">
            <el-select v-model="searchForm.deptId" placeholder="选择部门/异业合作企业" @visible-change="getDeptList" :loading="deptLoading">
              <el-option v-for="item in deptOptions" :label="item.label" :value="item.value" :key="item.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="principal">
            <el-select v-model="searchForm.principal" placeholder="选择担当">
              <el-option label="全部" :value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="type">
            <el-select v-model="searchForm.type" placeholder="选择活动类型">
              <el-option label="全部" :value="1"></el-option>
              <el-option label="常规" :value="2"></el-option>
              <el-option label="节点" :value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="belong">
            <el-select v-model="searchForm.belong" placeholder="选择业务归属">
              <el-option label="全部" :value="1"></el-option>
              <el-option label="客户业务" :value="2"></el-option>
              <el-option label="DLR业务" :value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="mode">
            <el-select v-model="searchForm.mode" placeholder="选择活动模式">
              <el-option label="全部" :value="1"></el-option>
              <el-option label="线上" :value="2"></el-option>
              <el-option label="线下" :value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="status">
            <el-select v-model="searchForm.status" placeholder="选择活动状态">
              <el-option label="全部" :value="1"></el-option>
              <el-option label="申请中" :value="2"></el-option>
              <el-option label="已通过" :value="3"></el-option>
              <el-option label="已开始" :value="4"></el-option>
              <el-option label="已暂停" :value="5"></el-option>
              <el-option label="已重启" :value="6"></el-option>
              <el-option label="未通过" :value="7"></el-option>
              <el-option label="已取消" :value="8"></el-option>
              <el-option label="已编辑" :value="9"></el-option>
              <el-option label="已结束" :value="10"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="unissued">
            <el-select v-model="searchForm.unissued" placeholder="选择未发行额度状态">
              <el-option label="全部" :value="1"></el-option>
              <el-option label="已冻结" :value="2"></el-option>
              <el-option label="已返还" :value="3"></el-option>
              <el-option label="已作废" :value="4"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="dateRange">
            <el-new-date-picker v-model="searchForm.dateRange"></el-new-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button plain type="primary" icon="el-icon-search" @click="onSearch">查询</el-button>
            <el-button plain icon="el-icon-refresh-right" @click="onReset">重置</el-button>
          </el-form-item>
        </el-form>
      </el-new-card>

      <!-- 查询结果 -->
      <el-new-card>
        <template slot="header">
          <span>活动列表</span>
          <el-button plain icon="el-icon-plus" @click="toCreateAct">创建活动</el-button>
        </template>

        <el-new-table :apiName="apiName" ref="table">
          <el-table-column prop="activity" label="活动名称" align="center" min-width="120">
            <template slot-scope="scope">
              <el-link type="primary" @click="toActDetail(scope.row)">{{ scope.row.activity }}</el-link>
            </template>
          </el-table-column>
          <el-table-column prop="deptName" label="部门/企业" align="center"></el-table-column>
          <el-table-column prop="principal" label="担当" align="center"></el-table-column>
          <el-table-column prop="type" label="活动类型" align="center"></el-table-column>
          <el-table-column prop="belong" label="业务归属" align="center"></el-table-column>
          <el-table-column prop="mode" label="活动模式" align="center"></el-table-column>
          <el-table-column prop="plat" label="平台" align="center"></el-table-column>
          <el-table-column prop="status" label="活动状态" align="center">
            <template slot-scope="scope">
              <act-sts-icon :status="scope.row.status" />
            </template>
          </el-table-column>
          <el-table-column prop="unissued" label="未发行额度状态" align="center" min-width="120px"></el-table-column>
          <el-table-column prop="date" label="开始时间" align="center" min-width="150px">
            <template slot-scope="scope">
              <span>{{ scope.row.date | fmtDateTime }}</span>
            </template>
          </el-table-column>
        </el-new-table>
      </el-new-card>
    </template>

    <router-view v-else />
  </div>
</template>

<script>
import ElNewCard from '@/components/ElNewCard'
import ElNewTable from '@/components/ElNewTable'
import ElNewDatePicker from '@/components/ElNewDatePicker'
import ActStsIcon from '@/components/status/ActStsIcon'
import { mapGetters } from 'vuex'

export default {
  name: 'credit-detail',
  components: {
    ElNewCard,
    ElNewTable,
    ElNewDatePicker,
    ActStsIcon
  },
  data () {
    return {
      apiName: 'serviceGetActList',
      searchForm: {
        activity: '',
        deptId: '',
        principal: '',
        type: '',
        belong: '',
        mode: '',
        status: '',
        unissued: '',
        dateRange: []
      },
      deptLoading: false,
      deptOptions: []
    }
  },
  computed: {
    ...mapGetters({
      accountInfo: 'getAccountInfo'
    })
  },
  methods: {
    onSearch () {
      this.$refs.table.reset()
    },

    onReset () {
      this.$refs.searchForm.resetFields()
    },

    // 获取各部门列表
    getDeptList (value) {
      if (value && this.deptOptions.length <= 0) {
        this.deptLoading = true
        this.$http.serviceGetDeptList()
          .then(res => {
            this.deptOptions = res.map(r => ({
              value: r.id,
              label: r.name
            }))
          })
          .catch(() => {
            this.deptOptions = []
          })
          .finally(() => {
            this.deptLoading = false
          })
      }
    },

    toCreateAct () {
      this.$router.push('/activityMgmt/createActAppl')
    },

    toActDetail (row) {
      let url = ''

      if (row.status === '01' || row.status === '10' || row.status === '11' || row.status === '20' || row.status === '90') {
        url = '/activityMgmt/actDetail'
      } else if (row.status === '00' || row.status === '02' || row.status === '03' || row.status === '30') {
        url = '/activityMgmt/actApplDetail'
      }

      url && this.$router.push({ path: url, query: { id: row.id } })
    }
  }
}
</script>

